html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  font:
    12px/1 Tahoma,
    Helvetica,
    Arial,
    '\5b8b\4f53',
    sans-serif;
  color: white;
  background-color: black;
}

.text-red {
  color: #e74f4d;
}

@keyframes orbit-colored {
  0% {
    border-color: rgb(74 213 215);
  }

  25% {
    border-color: rgb(61 241 34);
  }

  50% {
    border-color: rgb(147 115 37);
  }

  75% {
    border-color: rgb(177 16 191);
  }

  100% {
    border-color: rgb(203 14 4);
  }
}

.orbit {
  position: fixed;
  top: 27%;
  left: 30%;
  border: 3px solid white;
  animation: orbit-colored 3s ease-in infinite alternate;
}

#orbit0 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

#orbit1 {
  width: 200px;
  height: 200px;
  margin-top: -50px;
  margin-left: -50px;
  border-radius: 50%;
}

#orbit2 {
  width: 300px;
  height: 300px;
  margin-top: -100px;
  margin-left: -100px;
  border-radius: 50%;
}

#orbit3 {
  width: 400px;
  height: 400px;
  margin-top: -150px;
  margin-left: -150px;
  border-radius: 50%;
}

