.spaceship {
  position: fixed;
  top: 26%;
  left: 32%;
  color: #e74f4d;
  background-color: white;
  border: 1px solid #e74f4d;
  transform: rotate(0deg);

  /* animation: ship-rotate 3s linear infinite normal; */
}

#ship0 {
  transform-origin: center 60px;
}

#ship1 {
  margin-top: -50px;
  transform-origin: center 110px;
}

#ship2 {
  margin-top: -100px;
  transform-origin: center 160px;
}

#ship3 {
  margin-top: -150px;
  transform-origin: center 210px;
}

.panel {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 30%;
  background-color: white;
  border: 2px solid #e74f4d;
  border-radius: 3%;
}

.logger {
  position: fixed;
  right: 0;
  width: 30%;
  height: 70%;
  overflow: scroll;
  color: #e74f4d;
  background-color: white;
}

.logger-list {
  line-height: 15px;
  list-style: none;
}
